$sub-color: rgb(0, 0, 0);
$main-color: black;

@mixin shrinkLabel {
	top: -5px;
	font-size: 12px;
	color: $main-color;
}

.group {
	position: relative;

	.form-input {
		background: none;
		background-color: rgba(255, 255, 255, 0);
		color: $sub-color;
		font-size: 18px;
		padding: 10px 10px 10px 5px;
		display: block;
		width: 100%;
		border: none;
		border-radius: 0;
		border-bottom: 1px solid $sub-color;
		margin: 10px 0;
		&:focus {
			outline: none;
		}

		&:focus ~ .form-input-label {
			@include shrinkLabel();
		}
	}

	input[type="password"] {
		letter-spacing: 0.3em;
	}

	.form-input-label {
		color: $sub-color;
		font-size: 16px;
		font-weight: normal;
		position: absolute;
		pointer-events: none;
		left: 5px;
		top: 20px;
		transition: 300ms ease all;

		&.shrink {
			@include shrinkLabel();
		}
	}
	.date-picker {
		color: rgb(255, 255, 255);
	}

	input[type="date"]:focus {
		color: black;
	}
	.date-picker-after {
		color: rgb(0, 0, 0);
	}
}
